@import url('https://fonts.googleapis.com/css?family=Lato:400&display=swap');
@import url('https://fonts.googleapis.com/css?family=Amatic+SC:400&display=swap');

h1{
  font-family: 'Amatic SC', cursive;
  font-weight:400;
  font-size:400%;
  text-align: center;
}

p{
  font-family: 'Lato', sans-serif;
  font-weight:400;
  text-align: center;
}
.results{
  min-height: 100vh;
  background: linear-gradient(150deg, #ffc837, #ff8008);
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 25px;
}

.search-button{
  background: #ff6308;
  border: none;
  margin-left: 15px;
  padding: 10px 20px;
  color: white;
  border-radius: 25px;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.h1{
  text-align: center;
}